@import url(https://fonts.googleapis.com/css?family=Press+Start+2P);
code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #003;
  padding-top: 50px;
  color: white;
  text-align: center;
  box-sizing: inherit;
}

html {
  font-size: 62.5%;
  box-sizing: border-box;
}


.Cell {
  height: 10rem;
  width: 10rem;
  background-color: rgb(53, 0, 199);
  border-radius: 5px;
}

@media only screen and (max-width: 31.25em) {
  .Cell {
    height: 6rem;
    width: 6rem;
  }
}

.Cell-lit {
  background-color: rgb(255, 255, 255);
}
.Board {
  display: flex;
  flex-direction: column;
  height: 50vh;
  justify-content: center;
}

.Board-table {
  margin: 0 auto;
  box-shadow: 0 0 3.5rem rgb(124, 240, 255);
}


.Board-heading {
  color: #426DFB;
  font-size: 5rem;
  text-shadow: 0 0 1rem #2356FF;
 font-family: "Press Start 2P";
  animation: flux 3s linear infinite;
  -moz-animation: flux 3s linear infinite;
  -webkit-animation: flux 3s linear infinite;
  -o-animation: flux 3s linear infinite;
}




@-webkit-keyframes flux {

  0%,
  100% {
    text-shadow: 0 0 1rem #1041FF, 0 0 3rem #1041FF, 0 0 10rem #1041FF, 0 0 10rem #1041FF, 0 0 .4rem #8BFDFE, .5rem .5rem .1rem #147280;
    color: #28D7FE;
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  50% {
    text-shadow: 0 0 .5rem #082180, 0 0 1.5rem #082180, 0 0 5rem #082180, 0 0 5rem #082180, 0 0 .2rem #082180, .5rem .5rem .1rem #0A3940;
    color: #146C80;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
}




@keyframes flux {

  0%,
  100% {
    text-shadow: 0 0 1rem #1041FF, 0 0 3rem #1041FF, 0 0 10rem #1041FF, 0 0 10rem #1041FF, 0 0 .4rem #8BFDFE, .5rem .5rem .1rem #147280;
    color: #28D7FE;
    -webkit-transform: scale(1);
            transform: scale(1);
  }

  50% {
    text-shadow: 0 0 .5rem #082180, 0 0 1.5rem #082180, 0 0 5rem #082180, 0 0 5rem #082180, 0 0 .2rem #082180, .5rem .5rem .1rem #0A3940;
    color: #146C80;
    -webkit-transform: scale(1.05);
            transform: scale(1.05);
  }
}

@media (max-width: 65rem) {
  #container {
    width: 100%;
  }

    .Board-heading {
      font-size: 3.5em;
    }
  }
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

